@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Quicksand:wght@300..700&display=swap");
@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.11.3/font/bootstrap-icons.min.css");
@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  margin: 0px;
  padding: 0px;
  font-family: "Poppins", serif;
}

.active hr {
  @apply block;
}
.active li{
  color: black;
}

.banner-section {
  height: 300px;
  background-image: linear-gradient(to top, #cfc4f5, #ffffff);
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding-top: 20px;
  border-bottom-left-radius: 24px;
  border-bottom-right-radius: 24px;
}

.banner-title > p {
  font-weight: bold;
  font-size: 1.75rem;
}

.banner-title,
.banner-img {
  width: 35%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-container {
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.search-container {
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  background-color: #f0f0f5;
  border-radius: 8px;
  padding-left: 15px;
  font-size: 1rem;
  border: none;
  outline: none;
  margin-left: 10px;
  height: 40px;
  width: 25vw;
  min-width: 300px;
}

.res-card-container {
  display: flex;
  flex-wrap: wrap;
  padding-left: 10vw;
}

.res-card {
  width: 250px;
  height: 290px;
  margin: 15px;
  transition: all 0.8s;
}
.res-card:hover {
  transform: scale(0.9);
}

.res-card-img {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 18px;
  width: 100%;
  height: 60%;
  overflow: hidden;
  margin-bottom: 8px;
}

.res-card-details {
  padding: 10px;
}
